const uni = require('uni').default;


















import pageHead from '../../../components/page-head.vue'

export default {
	data() {
		return {
			title: 'onCompassChange',
			direction: 0
		}
	},
	onReady: function () {
		uni.onCompassChange((res) => {
			this.direction = parseInt(res.direction)
		})
	},
	onUnload() {
		uni.startCompass();
		this.direction = 0;
	},
	components: {
		pageHead
	}
}



